import { Component, OnInit, Input, Inject } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { GithubService } from './github.service';

import 'rxjs/add/operator/map';

@Component({
  selector: 'ng-gh-btn',
  template: `
  <span [ngClass]="{'github-btn':size!=='large', 'github-btn-large':size==='large'}" id="github-btn">
    <a class="gh-btn" [ngClass]="mainClass" id="gh-btn" target="_blank" aria-label="" href="{{buttonHref}}">
        <span class="gh-ico" aria-hidden="true"></span>
    <span class="gh-text" id="gh-text">{{buttonType}}</span>
    </a>
    <a *ngIf="count==='true'" class="gh-count" id="gh-count" href="{{counterHref}}" target="_blank" aria-label="">{{counterNum | number}}</a>
  </span>
`,
  styles: [`
  body {
  padding: 0;
  margin: 0;
  font: bold 11px/14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  overflow: hidden;
}
.github-btn {
  height: 20px;
  overflow: hidden;
}
.gh-btn,
.gh-count,
.gh-ico {
  float: left;
}
.gh-btn,
.gh-count {
  padding: 2px 5px 2px 4px;
  color: #333;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
  border-radius: 3px;
}
.gh-btn {
  background-color: #eee;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #fcfcfc), color-stop(100%, #eee));
  background-image: -webkit-linear-gradient(top, #fcfcfc 0, #eee 100%);
  background-image: -moz-linear-gradient(top, #fcfcfc 0, #eee 100%);
  background-image: -ms-linear-gradient(top, #fcfcfc 0, #eee 100%);
  background-image: -o-linear-gradient(top, #fcfcfc 0, #eee 100%);
  background-image: linear-gradient(to bottom, #fcfcfc 0, #eee 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fcfcfc', endColorstr='#eeeeee', GradientType=0);
  background-repeat: no-repeat;
  border: 1px solid #d5d5d5;
}
.gh-btn:hover,
.gh-btn:focus {
  text-decoration: none;
  background-color: #ddd;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #eee), color-stop(100%, #ddd));
  background-image: -webkit-linear-gradient(top, #eee 0, #ddd 100%);
  background-image: -moz-linear-gradient(top, #eee 0, #ddd 100%);
  background-image: -ms-linear-gradient(top, #eee 0, #ddd 100%);
  background-image: -o-linear-gradient(top, #eee 0, #ddd 100%);
  background-image: linear-gradient(to bottom, #eee 0, #ddd 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#eeeeee', endColorstr='#dddddd', GradientType=0);
  border-color: #ccc;
}
.gh-btn:active {
  background-image: none;
  background-color: #dcdcdc;
  border-color: #b5b5b5;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15);
}
.gh-ico {
  width: 14px;
  height: 14px;
  margin-right: 4px;
  background-image: url('data:image/svg+xml;base64,');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.gh-count {
  position: relative;
  margin-left: 4px;
  background-color: #fafafa;
  border: 1px solid #d4d4d4;
}
.gh-count:hover,
.gh-count:focus {
  color: #4183C4;
}
.gh-count:before,
.gh-count:after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}
.gh-count:before {
  top: 50%;
  left: -3px;
  margin-top: -4px;
  border-width: 4px 4px 4px 0;
  border-right-color: #fafafa;
}
.gh-count:after {
  top: 50%;
  left: -4px;
  z-index: -1;
  margin-top: -5px;
  border-width: 5px 5px 5px 0;
  border-right-color: #d4d4d4;
}
.github-btn-large {
  height: 30px;
}
.github-btn-large .gh-btn,
.github-btn-large .gh-count {
  padding: 3px 10px 3px 8px;
  font-size: 16px;
  line-height: 22px;
  border-radius: 4px;
}
.github-btn-large .gh-ico {
  width: 20px;
  height: 20px;
}
.github-btn-large .gh-count {
  margin-left: 6px;
}
.github-btn-large .gh-count:before {
  left: -5px;
  margin-top: -6px;
  border-width: 6px 6px 6px 0;
}
.github-btn-large .gh-count:after {
  left: -6px;
  margin-top: -7px;
  border-width: 7px 7px 7px 0;
}
`]
})
export class GithubButton implements OnInit {
  @Input() username: string;
  @Input() reponame: string;
  @Input() type: string;
  @Input() count: string;
  @Input() size: string;

  public user: any;
  public repo: any;
  public mainClass: string;
  public buttonHref: string;
  public buttonType: string;
  public counterHref: string;
  public counterNum: number;

  constructor( @Inject(GithubService) private github: GithubService) {

  }

  ngOnInit() {
    if (this.reponame && this.type !== 'follow') {
      this.github.getRepoInfo(this.username, this.reponame).subscribe(repo => {
        this.repo = repo.json();
        this.process();
      });
    } else if (this.type === 'follow') {
      this.github.getUserInfo(this.username).subscribe(user => {
        this.user = user.json();
        this.process();
      });
    }
  }

  process() {
    switch (this.type) {
      case 'watch':
        this.mainClass += ' github-watchers';
        this.buttonType = 'Watch';
        this.buttonHref = 'https://github.com/' + this.username + '/' + this.reponame + '/';
        this.counterHref = 'https://github.com/' + this.username + '/' + this.reponame + '/watchers';
        this.counterNum = this.repo.watchers_count;
        break;
      case 'star':
        this.mainClass += ' github-stargazers';
        this.buttonType = 'Star';
        this.buttonHref = 'https://github.com/' + this.username + '/' + this.reponame + '/';
        this.counterHref = 'https://github.com/' + this.username + '/' + this.reponame + '/stargazers';
        this.counterNum = this.repo.stargazers_count;
        break;
      case 'fork':
        this.mainClass += ' github-forks';
        this.buttonType = 'Fork';
        this.buttonHref = 'https://github.com/' + this.username + '/' + this.reponame + '/fork';
        this.counterHref = 'https://github.com/' + this.username + '/' + this.reponame + '/network';
        this.counterNum = this.repo.forks_count;
        break;
      case 'follow':
        this.mainClass += ' github-me';
        this.buttonType = 'Follow @' + this.username;
        this.buttonHref = 'https://github.com/' + this.username;
        this.counterHref = 'https://github.com/' + this.username + '/followers';
        this.counterNum = this.user.followers;
        break;
    }
  }
}



